import React from "react"
import { Link } from "gatsby"

import "../styles/style.css"
import logo from "./logo.png"
import con1 from "./contact1.jpg"
import con2 from "./contact2.jpg"
import con3 from "./contact3.jpg"

export default function Home() {
  return (
    <>
    <main>
  <title>Counsel Connect Home</title>
  <body>
  <div className="nav">
      <img className="logo" src={logo} alt="logo" />
        <nav>
          <ul>
          <li>
              <Link to="/">Home</Link>
            </li>
            <li>
              <Link to="/Contact">Contact Us</Link>
            </li>
            <li>
              <Link to="/Programs">Programs</Link>
            </li>
            <li>
              <Link to="/FAQ">FAQ</Link>
            </li>
          </ul>
        </nav>
    </div>
    <div class="page">
<div class="One">
		<h2>Contact 1</h2>
		<img className="Contact" src={con1} alt="con1" />
		<p> Name: Joan Johnson <br></br>
			Title: Head Manager <br></br>
			E-Mail: joanj@email.com <br></br>
			Phone: (804)111-1111</p>
	</div>
	<div class="Two">
		<h2>Contact 2</h2>
        <img className="Contact" src={con2} alt="con2" />
		<p> 
            Name: John Doe <br></br>
			Title: Community Organizer <br></br>
			E-Mail: johnd@email.com <br></br>
			Phone: (804)222-2222</p>
	</div>
	<div class="Three">
		<h2>Contact 3</h2>
		<img className="Contact" src={con3} alt="con3" />
		<p> Name: Sammy Jenkins <br></br>
			Title: Clinical Worker <br></br>
			E-Mail: sammyj@email.com <br></br>
			Phone: (804)333-3333</p>
	</div>
</div>
    </body>
    </main>
    </>
  )
}
